@mixin  flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.formContainer
{
    @include flex;
    width: 100%;
    min-height: 100vh;
    position: absolute;
    background-color: #ececec;
    top: 100%;
    animation: intro 0.3s ease forwards;
    @keyframes intro {
        0%{
            top: 100%;
        }
        100%{
            top: 0%;
        }
    }
    .saving
    {
        @include flex;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 20;
        font-size: 40px;
        font-weight: 700;
        font-family: monospace;
        background-color: rgba(0, 0, 0, 0.342);
        div 
        {
            @include flex;
            justify-content: space-evenly;
            flex-direction: column;
            border-radius: 25px;
            width: 80vw;
            height: 150px;
            padding: 20px;
            background-color: white;
            box-shadow: 0px 0px 5px 6px rgb(143, 143, 143), 
            0 10px 5px rgba(0,0,0,0);
        }
    }
    .offline
    {
        @include flex;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 20;
        font-size: 40px;
        font-weight: 700;
        font-family: monospace;
        background-color: rgba(0, 0, 0, 0.342);
        div 
        {
            @include flex;
            justify-content: space-evenly;
            flex-direction: column;
            border-radius: 25px;
            width: 80vw;
            height: 150px;
            padding: 20px;
            background-color: white;
            box-shadow: 0px 0px 5px 6px rgb(143, 143, 143), 
            0 10px 5px rgba(0,0,0,0);
            p {
                font-size: 20px;
                text-align: center;
            }
            button
            {
                background-color: red;
                border-radius: 25px;
                border: none;
                width: 80%;
                height: 30px;
                color: white;
                font-size: 20px;
                cursor: pointer;
                margin-top: 15px;
            }
        }
    }
    form
    {
        @include flex;
        justify-content: flex-start;
        flex-direction: column;
        width: 100%;
        min-height: 100vh;
        .titleInput
        {
            width: 95%;
            height: 50px;
            background: none;
            // border: 2px solid gray;
            border: none;
            border-radius: 15px;
            background: white;
            font-size: 25px;
            margin-top: 60px;
            outline: none;
            padding: 10px;
            box-shadow: 6px 6px 5px #bbb, 
                        0 10px 5px rgba(0,0,0,0);
        }
        textarea 
        {
            width: 95%;
            min-height: 450px;
            margin-top: 20px;
            background: none;
            // border: 2px solid gray;
            border: none;
            border-radius: 15px;
            font-size: 25px;
            outline: none;
            resize: none;
            padding: 10px;
            box-shadow: 6px 6px 5px #bbb, 
                        0 10px 5px rgba(0,0,0,0);
        }
        .selectContainer
        {
            @include flex;
            flex-direction: column;
            width: 95vw;
            height: 80px;
            position: relative;
            margin-top: 20px;
            margin-bottom: 15px;
        }
        .colorInput
        {
            @include flex;
            justify-content: space-evenly;
            width: 95%;
            height: 50px;
            .colorContainer
            {
                @include flex;
                position: relative;
                div
                {
                    position: absolute;
                    width: 20px;
                    height: 20px;
                    // background: #000;
                    border-radius: 50%;
                }
            }
        }
        button
        {
            background: rgb(201, 144, 255);
            border: none;
            width: 95%;
            height: 40px;
            font-size: 25px;
            color: white;
            border-radius: 15px;
            position: relative;
            overflow: hidden;
            box-shadow: 6px 6px 5px #bbb, 
                        0 10px 5px rgba(0,0,0,0);
            span
            {
            position: absolute;
            background: #fff;
            transform: translate(-50%, -50%);
            pointer-events: none;
            border-radius: 50%;
            animation: animate 0.5s linear infinite;
            }
            @keyframes animate {
                0%{
                    width: 0;
                    height: 0;
                    opacity: 0.5;
                }
                100%{
                    width: 500px;
                    height: 500px;
                    opacity: 0;
                }
            }
        }
    }
    .inputCloseBtn
    {
        @include flex;
        position: absolute;
        background: none;
        outline: none;
        border: none;
        width: 30px;
        height: 30px;
        right: 10px;
        top: 10px;
        cursor: pointer;
        div
        {
            position: absolute;
            width: 30px;
            height: 5px;
            background: gray;
            border-radius: 12px;
        }
        div:nth-child(1)
        {
            transform: rotate(45deg);
        }
        div:nth-child(2)
        {
            transform: rotate(-45deg);
        }    
    }
}
.close-1
{
    animation: close-1 0.5s forwards;
}
.close-2
{
    animation: close-2 0.5s forwards;
}
@keyframes close-1 {
    0%{transform: rotate(90deg) translateY(0px); width: 30px; opacity: 1;}
    100%{transform: rotate(0deg) translateY(0px); width:30px; opacity: 0;}
}
@keyframes close-2 {
    0%{transform: rotate(45deg) translateY(0px); width: 30px; opacity: 1;}
    100%{transform: rotate(0deg) translateY(0px); width:30px; opacity: 0;}
}